@media (max-width: 500px) {
    .app {
        margin: 100px auto 50px auto;
        width: 100%;
        height: 100%;
        font-family: Shamel, serif;
    }
    .div-invoice {
        margin: 30px auto 50px auto;
        width: 100%;
        height: 100%;
    }
    .header-line-right{
        height: 38px;
        width: 6%;
        background-color: #fc7c5b;
        position: absolute;
        margin-top: 28px;
    }
    .header-line{
        height: 2px;
        background-color: #fc7c5b;
        margin-top: 65px;
