    }
    .ms-k{
        font-size: 13px;
        font-family: Arial, serif;
        position: absolute;
        margin-top: 28px;
        margin-right: 10px;
        color: #0464cc;
        font-weight: bold;
    }
    .ms-k-ar{
        font-size: 19px;
        font-family: Shamel, serif;
        position: absolute;
        margin-right: 10px;
        color: #0464cc;
        font-weight: bold;
    }
    .header-details {
        margin-top: 3px;
    }
    .header-acount-number {
        margin-right: 10%;
    }
    .signature-img {
        bottom: 27%;
